@mixin font-roboto {
    font-family: 'Roboto', sans-serif;
}

.list-header {
    font-weight: 700;
}

.list-content {
    background-color: rgb(216, 246, 239);
}

.fade-block {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100vw;
    height: 100vw;
    background-color: rgba(8, 112, 32, 0.69);
}

.modal-window {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
}

.content-hidden {
    display: none;
}

.hidden {
    display: none;
}

[data-name], [data-tab] {
    cursor: pointer;
}
body.lock {
    overflow: hidden;
}
.popup {
    position: fixed;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.8);
    top: 0;
    left: 0;
    opacity: 0;
    visibility: hidden;
    overflow-y: auto;
    overflow-x: hidden;
    transition: all 0.8s ease 0s;
    z-index: 10; 
}
.popup.open {
    opacity: 1;
    visibility: visible;
}
.popup.open .popup-content {
    transform: perspective(600px) translate(0px, 0%)rotateX(0deg);
    opacity: 1;
}
.popup_area {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
}
.popup-body {
    min-height: 100%;
    min-width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 30px 10px;
    transition: all 0.8s ease 0s;
}
.popup-content {
    background-color: #fff;
    max-width: 800px;
    padding: 30px;
    position: relative;
    transition: all 0.8s ease 0s;
    opacity: 0;
    transform: perspective(600px) translate(0px, -100%)rotateX(45deg);
    margin-right: 30px;
    @include font-roboto;
}
.popup-close {
    position: absolute;
    right: 10px;
    top: 10px;
    font-size: 20px;
    text-decoration: none;
    color: #000;
}
.popup-title {
    font-size: 40px;
    margin: 0 0 1em 0px;
}
